// ----------------------------
// Navbar overload
// ---------------

// Overload Bootstrap behaviors for the navbar.

.navbar {
  // No border
  border: none;
}

// Display the navbar as raised
// above next content.
.navbar-raised {
  box-shadow: $default-box-shadow;
}

// Remove border
.navbar-toggle {
  border: none;
  color: $primary-text-color;
}
